import { Injectable } from '@angular/core';
import { DataStorageService } from '../data-storage/data-storage.service';
import { AnalyticsService } from '../analytics/analytics.service';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';

@Injectable({
  providedIn: 'root'
})
export class CustomEventService {

  constructor(private dataStorage: DataStorageService, private analyticsService: AnalyticsService) { }
  /**
   * This method is exposed to user to help pushing custom events
   * 
   * @param customEventName - Any name that user can be configure
   * @param eventData - Any data, which user configured in additional info
   */
  public pushEvent(customEventName: string, eventData: any): void {
    const analyticsBean: AnalyticsBean =
      this.analyticsService.setAnalyticsData(eventData, '', customEventName, '');
    this.dataStorage.appendToAnalyticsArray(analyticsBean);
  }
}
